html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}
body.fijar {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: rgba(5, 19, 22, 0.68);
  background: rgba(5, 19, 22, 0.5);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(17.5px);
  -webkit-backdrop-filter: blur(17.5px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}
@media (min-width: 768px) {
  .header.fijo {
    position: fixed;
    width: 100%;
    top: 0;
  }
}
.header_contenido {
  padding: 0.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header_contenido {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header_logo p {
  font-size: 4rem;
  font-weight: bold;
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.header_navegacion {
  display: flex;
  flex-direction: column;
  margin-top: 4rem;
  gap: 2rem;
}
.header_navegacion a {
  color: #FFFFFF;
  font-size: 2.5rem;
}
.header_navegacion a:hover {
  color: rgb(80, 127, 182);
}
@media (min-width: 768px) {
  .header_navegacion {
    flex-direction: row;
    margin: 0;
  }
}

.video {
  position: relative;
  max-height: 90vh;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(0, 0, 0);
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.9612219888) 13%, rgba(94, 178, 198, 0.381390056) 79%);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video_contenido {
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
.video h2 {
  color: #FFFFFF;
  font-size: 4rem;
  margin: 0;
  text-align: center;
  -webkit-text-stroke: 2px rgba(35, 18, 18, 0.56);
}
.video p {
  color: #FFFFFF;
  background-color: rgba(17, 35, 95, 0.7294117647);
  display: inline-block;
  padding: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
}
@media (min-width: 768px) {
  .video p {
    font-size: 3rem;
  }
  .video h2 {
    font-size: 8rem;
  }
}
.video video {
  background-size: cover;
  width: 100%;
  display: block;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: rgba(17, 35, 95, 0.7294117647);
  font-weight: 700;
}

.lineup {
  background-color: rgba(17, 35, 95, 0.7294117647);
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFFFFF;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

.escenario {
  padding-top: 2rem;
}
.escenario .nombre-escenario {
  font-weight: bold;
}
.escenario .nombre-escenario:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}
.escenario .blanco {
  color: #FFFFFF;
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: rgb(80, 127, 182);
}
.bg-amarillo li:nth-child(odd) {
  background-color: #375a83;
}

.bg-verde {
  background-color: rgba(5, 19, 22, 0.68);
  color: #FFFFFF;
}
.bg-verde li:nth-child(odd) {
  background-color: rgba(0, 0, 0, 0.68);
}

.galeria h3 {
  text-align: center;
  padding-top: 5rem;
}
.galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 2rem;
  padding: 0;
  justify-content: center;
}
.galeria-imagenes img {
  border-radius: 10px;
  cursor: pointer;
}

.overlayImagen {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: grid;
  place-items: center;
  z-index: 2;
}
.overlayImagen picture {
  display: block;
  max-width: 60%;
  z-index: 1;
}
.overlayImagen picture img {
  border-radius: 10px;
}
.overlayImagen .botonCerrar {
  background-color: white;
  color: black;
  width: 4rem;
  height: 4rem;
  display: grid;
  place-content: center;
  padding: 1rem;
  font-size: 2.4rem;
  font-weight: bold;
  border-radius: 50%;
  border: 1px solid red;
  cursor: pointer;
}

.site-footer {
  background-color: rgba(17, 35, 95, 0.7294117647);
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
}

.boletos {
  padding: 5rem 0;
  background-image: url("../img/grande/10.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
}
.boletos h3 {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  color: black;
}
.pase .nombre-pase {
  text-align: center;
  font-size: 2.8rem;
  color: #FFFFFF;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}
.pase-header {
  background-color: rgba(17, 35, 95, 0.7294117647);
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #FFFFFF;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #FFFFFF;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: rgba(79, 136, 204, 0.8);
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.overlayBoletos {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(5, 19, 22, 0.68);
  top: 0;
  z-index: 1;
}

.contenedor-boletos {
  position: relative;
  z-index: 2;
}

.boletos h3 {
  position: relative;
  z-index: 2;
}